aplican conceptos, prueban sus conocimientos y reciben retroalimentación inmediata que les ayuda a corregir errores y reforzar el aprendizaje.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Accesibilidad"}},[_c('p',[_vm._v("Estos recursos consideran las necesidades de todos los estudiantes, incluyendo aquellos con discapacidades, y a menudo se adhieren a pautas como las Directrices de Accesibilidad para el Contenido Web (WCAG) ("),_c('em',[_vm._v("Web Content Accessibility Guidelines")]),_vm._v("). El enfoque en la accesibilidad subraya la importancia de la inclusión en la educación virtual. Esto implica que los creadores deben conocer los estándares y las mejores prácticas de accesibilidad para garantizar que sus recursos sean utilizables por personas con una amplia gama de habilidades y discapacidades. Esto podría implicar proporcionar texto alternativo para las imágenes, subtítulos para los videos y garantizar la navegación mediante teclado, lo que no solo beneficia a los usuarios con discapacidades, sino que también puede mejorar la usabilidad para todos.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Flexibilidad"}},[_c('p',[_vm._v("Pueden utilizarse en diversas situaciones de aprendizaje, adaptándose a diferentes enfoques pedagógicos y necesidades de los estudiantes. Pueden apoyar tanto el aprendizaje sincrónico como el asincrónico. Debido a que los RED pueden accederse en cualquier momento y lugar, ofrecen a los estudiantes la flexibilidad de estudiar a su propio ritmo y revisar los materiales, según sea necesario.")])])])],1)]),_c('div',{staticClass:"row align-items-start mb-5"},[_vm._m(4),_c('div',{staticClass:"col-lg-8"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Flexibilidad"}},[_c('p',[_vm._v("Pueden utilizarse en diversas situaciones de aprendizaje, adaptándose a diferentes enfoques pedagógicos y necesidades de los estudiantes. Pueden apoyar tanto el aprendizaje sincrónico como el asincrónico. Debido a que