ducativas, lo que los hace más versátiles y accesibles a una audiencia más amplia de educadores y estudiantes. Esto reduce las barreras técnicas para la adopción y el uso de recursos digitales.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Portabilidad"}},[_c('p',[_vm._v("Los RED deben ser accesibles a través de diferentes dispositivos y sistemas operativos, lo que asegura que los estudiantes puedan utilizar los RED sin importar el dispositivo que emplee. Esto pone de relieve la importancia de un diseño responsivo y la compatibilidad multiplataforma. En el actual panorama tecnológico, los estudiantes emplean diversos dispositivos para acceder a contenido en línea. Los RED portátiles aseguran que los materiales de aprendizaje sean accesibles en computadoras de escritorio, portátiles, tabletas y teléfonos inteligentes, ofreciendo una experiencia de aprendizaje coherente y conveniente.")])])])],1)]),_c('p',{staticClass:"mb-5"},[_vm._v("La siguiente tabla resume las características claves de los Recursos Educativos Digitales:")]),_vm._m(5),_vm._m(6),_c('div',{staticClass:"bg-full-width border-top color-primario"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('h2',{attrs:{"data-aos":"fade-left"}},[_vm._v("MATERIAL COMPLEMENTARIO")]),_c('div',{staticClass:"row material-complementario"},[_c('div',{staticClass:"col-12 col-md-6 col-lg-7"},[_c('p',[_vm._v("Los invitamos a explorar el material complementario de este curso, en esta sección encontrará recursos que le permitirán profundizar y enriquecer su aprendizaje en los temas tratados en esta unidad.")]),_c('p',{staticClass:"d-flex my-4"},[_c('img',{staticClass:"me-3",style:({'max-width':'16px'}),attrs:{"src":require("@/assets/componentes/link.svg")}}),_c('a',{attrs:{"href":"https://www.redalyc.org/pdf/4985/498573055005.pdf ","target":"_blank","rel":"noopener noreferrer"}},[_vm._v("Haak, L. S. (2005). Recursos educativos digitales Procesos de mediación y mediatización en la comunicación pedagógica. Revista Digital de Investi